}

.loader:before {
	display: block;
	position: absolute;
	content: "";
	left: -200px;
	width: 220px;
	height: 4px;
	background-color: black;
	animation: loading 2s linear infinite;
}

@keyframes loading {
	from {
		left: -200px;
		width: 30%;
	}
	50% {
		width: 30%;
	}
	70% {
		width: 70%;
	}
	80% {
		left: 50%;
	}
	95% {
		left: 120%;
