.confirmDelete {
	position: fixed;
	display: none;
	z-index: 1005;
	height: 100%;
	background: rgb(0, 0, 0);
	/* other browser support */
	background: rgba(0, 0, 0, 0.9);
	opacity: 0.4;
	transition: 0.2s;
}

.confirmDeleteBox {
	height: 110px;
	top: -200px;
	position: absolute;
	border: 1px solid #00c851;
	color: white;
	transition: 0.3s all ease-out;
	background: linear-gradient(#1D1D1D, #000000);
}

.areyousure {
	text-align: center;
	position: relative;
}

.areyousure span {
	font-family: 'Fjalla One', sans-serif;
	letter-spacing: 1px;
}

.delete_post_button,
.cancel_delete_post_button {
	position: relative;
	width: 100px;
	border: 1px solid #00c851;
	text-align: center;
	cursor: pointer;
	font-family: 'Fjalla One', sans-serif;
	padding: 2px;
}

.delete_post_button {
	background-color: #F83C3C;
}

.delete_post_button:hover {
	background-color: #FC1818;
}

.cancel_delete_post_button:hover {
	background-color: white;
	color: black;
}


/* --------------------- User control panel ------------- */

.bg_cp_user {
	display: none;
}

.show_bg_cp_user {
	display: block;
}

.user_cp_box {
	position: fixed;
	border: solid #00c851;
	border-top: none;
	height: auto;
	z-index: 1006;
	border-bottom-left-radius: 4px;
	border-bottom-right-radius: 4px;
	right: 0;
	background-color: #2C2B2B;
}

.user_cp_image {
	width: 60px;
	height: 60px;
	border: 1px solid white;
	padding: 4px;
}

.user_cp_loader {
	opacity: 0;
	margin-top: 7px;
	height: 4px;
	width: 100%;
	position: relative;
	overflow: hidden;
	background-color: #2C2B2B;
}

.user_cp_loader:before {
	display: block;
	position: absolute;
	content: "";
	left: -200px;
	width: 220px;
	height: 4px;
	background-color: white;
	animation: loading 2s linear infinite;
}

@keyframes user_cp_loader {
	from {
		left: -200px;
		width: 30%;
	}
	50% {
		width: 30%;
	}
	70% {
		width: 70%;
	}
	80% {
		left: 50%;
	}
	95% {
		left: 120%;
	}
	to {
		left: 100%;
	}
}

.cp_customFileLabel {
	height: 25px;
	line-height: 11px;
	border: 1px solid white;
	font-family: 'Fjalla One', sans-serif;
	font-size: 14px;
}

.cp_customFileLabel::after {
	height: 24px;
	line-height: 11px;
	font-family: 'Fjalla One', sans-serif;
	font-size: 13px;
}

.cp_changePFWrap {
	padding-top: 8px;
	padding-bottom: 15px;
}

.cp_changePFButton {
	color: #00c851;
	cursor: pointer;
	border: solid black;
	border-radius: 5px;
	background-color: white;
	padding: 5px;
	font-weight: bold;
	font-size: 10px;
	letter-spacing: 1px;
}

.cp_changePFButton:active {
	font-size: 9.5px;
}

.custom_file_wrap {
	border: 1px solid red;
}

.user_total_msg {
	color: white;
	font-size: 15px;
	font-family: 'Roboto Slab', serif;
}

.user_total_msg span {
	font-weight: bold;
}

.user_cp_arrow {
	color: white;
	width: 25px;
	height: 25px;
	margin-top: 7px;
	margin-right: 35px;
	background: url('../images/downArrow.png');
	background-repeat: no-repeat;
	background-size: cover;
}

.acc_cr_row {
	padding-top: 3px;
	border-top: 1px solid #00c851;
}

.account_created {
	padding-top: 10px;
}

.account_created,
.last_sigin {
	color: white;
	font-size: 11px;
	font-family: 'Roboto Slab', serif;
	font-weight: bold;
}

.account_created span,
.last_sigin span {
	color: #00c851;
	margin-left: 9px;
}

.signout_div {
	color: white;
	font-size: 14px;
	font-family: 'Fjalla One', sans-serif;
	text-decoration: underline;
	cursor: pointer;
	margin-bottom: 5px;
}

.signout_icon {
	display: inline-block;
	width: 15px;
	height: 15px;
	margin-left: 4px;
}

.signout_icon:hover {
	opacity: 1;
}


/* ------------ FOOTER ----------- */

.page-footer {
	background-color: #2C2B2B;
	height: 70px;
	color: white;
}

.go_to_top {
	width: 50px;
	height: 50px;
	display: block;
	margin-top: 7px;
	border: 1px solid #E2E2E2;
	margin-right: 25px;
	opacity: 0.7;
	background: url('../images/go_toTop.png');
	background-repeat: no-repeat;
	background-size: cover;
	transition: 0.5s;
}

.go_to_top:hover {
	opacity: 1;
}

.social_div {
	background-color: #2C2B2B;
	border-bottom: 1px solid #00c851;
}

.social_icon {
	display: block;
	width: 40px;
	height: 40px;
	opacity: 0.8;
}

.social_icon:hover {
	opacity: 1;
	cursor: pointer;
}

.github_icon {
	background: url('../images/github.png');
	background-size: cover;
	background-repeat: no-repeat;
}

.email_icon {
	background: url('../images/email.png');
	background-size: cover;
	background-repeat: no-repeat;
}

.linkedin_icon {
	background: url('../images/linkedin.png');
	background-size: cover;
	background-repeat: no-repeat;
}

.social_email {
	opacity: 0;
	top: -2px;
	text-align: center;
	margin-left: 5px;
	font-family: 'Roboto Slab', serif;
	font-size: 10px;
	transition: 0.5s all ease-out;
	color: #C5C3C3;
}

.social_email_show {
	opacity: 1;
	top: 10px;
	font-size: 16px;
	margin-bottom: 40px;
}

.allrights_div {
	padding-top: 4px;
	padding-bottom: 4px;
	background-color: #2C2B2B;
	color: #C5C3C3;
	font-family: 'Roboto Slab', serif;
	font-size: 13px;
}

